
import { useState,useEffect } from "react";

export default function GetJsonArr() {
  const [jsonArr, setJsonArr] = useState([])
  useEffect(() => {
    fetch('https://raw.githubusercontent.com/Andr7st/index/main/src/shared/redirect.json')
    .then(response => response.json())
    .then(datos => {
      setJsonArr(datos)
    })
  }, [])
  return jsonArr
}


