//import { Helmet } from 'react-helmet'
import StartLogo from '../../../components/Logo'
import LinkToAndr7stIndex from '../../../components/Andr7stIndex'
//import internetLinksTo from '../assets/data'
import GetJsonArr from './Search'

const ArregloJson = ( myMaraMetroABuscar ) => {
  let parametro = myMaraMetroABuscar
  const myJson = GetJsonArr()
  const datosFiltradosDelJson = myJson.filter((value) => (
    value.globalId.includes(parametro) 
  )
);
  return datosFiltradosDelJson
}

// javascipt function:
const redirectToAnotherSite = (siteLink, delaySecons) => {
  let timeSeconds = delaySecons;
  let timeTimerId = setInterval(function () {
    timeSeconds--;    
    if(timeSeconds === 0) {
      window.location.href = siteLink;
      clearInterval(timeTimerId);
    }
  }, 1000);
}



const RedirPage = ({parametroUrl}) => {

  //let urlParam = 'JV202' // Parametro por defecto en caso de no recibir ninguno
  //let jsArr_id       = 'lkIdDefault' // arr.lkId
  var jsArr_lkId     = 'default: jsArr_lkId' // arr.lkId
  let jsArr_globalId = 'jsArr_globalId' // arr.lkId
  let linkToRedirect = 'https://gist.github.com/Andr7st/'
  let redirectDelay = 3

  // Si se recibió parametro:
  if (parametroUrl) {
    // Parametro url se actualiza con el proporcionado:
    //urlParam = parametroUrl
    if (ArregloJson(parametroUrl)) {
      // No pude asignarlas tres al mismo tiempo, da error:
      //ArregloJson(parametroUrl).map(item => (
      //  jsArr_id = item.id
      //))
      ArregloJson(parametroUrl).map(item => (
        jsArr_lkId = item.lkId 
      ))
      ArregloJson(parametroUrl).map(item => (
        jsArr_globalId = item.globalId
      ))
    }
  }
  

  if (jsArr_globalId === 'jsArr_globalId') {
    redirectToAnotherSite(linkToRedirect, redirectDelay)
  }
  else {
    // Asigna link dependiendo del idLink
    linkToRedirect += jsArr_lkId
    //redireccionar al idLink
    redirectToAnotherSite(linkToRedirect, redirectDelay)
  }

  return (
    <div className="style-homePage">
      
      {/**
       * Provoca: 
       *  Warning: Using UNSAFE_componentWillMount in strict mode ...
      <Helmet><title> Andr7st | Redirect </title></Helmet>
      */}


      {/** En caso de introducir un codeUrl válido: */}
      {!(jsArr_globalId === 'jsArr_globalId') && 
      <div>
        <p> Andr7st | Redirect </p>
        <p>redirec:</p>
        <p> <a href={linkToRedirect}> {linkToRedirect} </a></p></div>
      }

      {/**Rediretcionar al link predeterminado */}
      {(jsArr_globalId === 'jsArr_globalId') && 
      <div>
        <p> Andr7st | Redirect </p>
        <p>redirec:</p>
        <p> <a href={linkToRedirect}> {linkToRedirect} </a></p></div>
      }

      {/** Logo + nickname: */}
      <div className="logo-start" ><StartLogo /></div>
        <br/>
        <LinkToAndr7stIndex />
        <br/>
    </div>
  )
}
export default RedirPage

